// Basic page reset


*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
//**************** CUSTOM SCROLL BAR ******************************************
body::-webkit-scrollbar{
    width: 8px;
}

body::-webkit-scrollbar-thumb{
    background-color: orange;
    border-radius: 50px;
}

body::-webkit-scrollbar-track{
    background-color: deepskyblue;

}



body{
    font-family: 'M PLUS Rounded 1c', sans-serif;
    font-size: 1.2rem;
    color: #a4acc4;
    padding: 5px;
}

span{
    color: #037FFF;
}