.ServiceSection{

.Services{
    width: 85%;
    border-top: 5px solid orangered;
    margin-top: 5rem;
    &:hover{
        border-top: 5px solid lightblue;
    }

    .service-content{
    height: 100%;
    padding: 2rem ;
    img{
        height: 200px;
    }

    .service-title{
        color: red;
        font-size: 1.6rem;
        font-weight: 500;
        position: relative;
        padding-bottom: 1rem;
        margin: 1rem 0;
        &::after{
            content: " ";
            position: absolute;
            bottom: 0;
            left: 0;
            height: 1px;
            width: 30%;
            background-color: lightblue;
        }
    }

    .service-text{
        color: green;
    }

}
}
}

.service-container{
    display: flex;
    justify-content: space-between;
}