.BlogPages{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap:2.5rem;
    .blog{
        width: 100%;
        padding: 1rem;   
        overflow: hidden; 
        background-color: orange; 
        .blog-content{
            overflow: hidden;
            padding-bottom: 2rem;
            img{
                width: 100%;
                height: 20rem;
                padding-bottom: 2rem;

                transition: all .2s ease-in-out;
                &:hover{
                    transform: scale(1.1);
                }
                
            }
            .blog-link{
                color: black;
                font-family: inherit;
                font-size: 2rem;
                text-decoration: none;
                margin: 2rem 0;
                padding-bottom: 2rem;
                transition: all .4s ease-in-out;
                &:hover{
                        color: lightseagreen;
                }
            }
        }
    }
}

.Blog-title{
    margin-bottom: 4rem;
}